$prefix: '.wc-booster';

#{$prefix}-product-review-wrapper {
	position: relative;
	// padding: 30px 15px;
	// margin: 0 -10px;

	#{$prefix}-product-review-card-wrapper {
	    > div {
	    	overflow: hidden;
	    }

		#{$prefix}-product-review-body-inner {
		    padding: 15px 0 0;
		}
	}

	.slick-slide {
		&:focus {
			outline: none;
		}
	}

	#{$prefix}-slider-arrow {
	    position: absolute;
	    right: 15px;
	    top: -30px;
	    z-index: 9;
	    cursor: pointer;
	    width: 45px;
	    height: 35px;
	    border: none;
	    background: #000;
	    color: #fff;
	    font-size: 21px;
	    display: flex;
	    align-items: center;
	    justify-content: center;
	    padding: 0;
		border-radius: 6px;
	    
	    &#{$prefix}-prev-arrow {
	    	right: 65px;
	    }
	}

	#{$prefix}-arrow-wide {
		#{$prefix}-slider-arrow {
			right: 8px;
			top: 50%;
			transform: translate(0, -50%);
			&#{$prefix}-prev-arrow {
				left: 8px;
				right: auto;
			}
		}
	}

	ul.slick-dots {
	    display: flex;
	    align-items: center;
	    justify-content: center;
	    list-style: none;
	    margin-bottom: 0;
	    padding-left: 0;
	    li {
	    	button {
	    		border: 1px solid transparent;
			    height: 9px;
			    width: 45px;
			    margin: 0 4px;
			    border-radius: 9px;
			    background: #e7e7e7;
			    cursor: pointer;

			    &:focus {
			    	outline: none;			    	
			    }			    
	    	}

	    	&.slick-active {
	    		button {
					background: #000;
	    		}
	    	}
	    }
	}

	.slick-track {
		margin-left: inherit;
	}
}

#{$prefix}-align-center {
    text-align: center;
	a {
		img {
			margin: auto;
		}
	}
}

#{$prefix}-align-left {
    text-align: left;
    .wc-booster-product-review-rating .star-rating {
    	margin: 0;
    }
}

#{$prefix}-align-right {
    text-align: right;

	a {
		img {
			margin-left: auto;
		}
	}

	 .wc-booster-product-review-rating .star-rating {
    	margin: 0 0 0 auto;
    }
}


#{$prefix}-product-review-rating {
    display: block;
	.star-rating {
		float: none !important;
		margin: auto;
	}
}


.wc-booster-product-review-wrapper {
	ul.slick-dots {
	   	display: flex !important;
	    align-items: center;
	    justify-content: center;
	    list-style: none;
	    margin-bottom: 0;

	    li {
	    	
	    	button {
	    		font-size: 0;
		    	color: transparent;
		    	height: 	20px !Important;
		    	width: 20px !Important;
		    	border-radius: 50% !Important;
		    	margin: 0 3px;
		    	background: #eee;
	    	}
	    	&.slick-active {
	    		button {
		    		background: #000;

	    		}
	    	}
	    }
	}
}

.wc-booster-align-center {
	.wc-booster-product-review-body-inner,
	.wc-booster-product-review-product-img img,
	.wc-booster-product-review-avatar img {
		margin: auto;
	}
}

.wc-booster-align-left {
	.wc-booster-product-review-body-inner,
	.wc-booster-product-review-product-img img,
	.wc-booster-product-review-avatar img {
		margin-right: auto;
	}
}

.wc-booster-align-right {
	.wc-booster-product-review-body-inner,
	.wc-booster-product-review-product-img img,
	.wc-booster-product-review-avatar img {
		margin-left: auto;
	}
}

.wc-booster-product-review-date-author span {
    margin-right: 10px;
}
.wc-booster-product-review-title a{
	text-decoration: none;
}

.woocommerce .wc-booster-product-review-init .slick-dots button {
    padding: unset;
}



.wc-booster-product-review-layout-two {
	.wc-booster-product-review-inner-wrapper {
		display: flex;
		align-items: center;
		justify-content: space-between;
		.wc-booster-product-review-product-img {
			max-width: 38%;
			flex: 0 0 38%;
			overflow: hidden;
			img {
				object-fit: cover;
			}
		}
		.wc-booster-product-review-meta {
			max-width: 58%;
			flex: 0 0 58%;
		}
	}
	
}